Office.onReady(() => {
  const dd = document.getElementById("macroSelect");
  macros.forEach((m, i) => {
    const opt = document.createElement("option");
    opt.value = i;
    opt.textContent = m.name;
    dd.appendChild(opt);
  });
  window.macros = macros;

  // Bind exactly one click handler:
  const btn = document.getElementById("insertBtn");
  btn.onclick = async () => {
    // Optional: log for debug
    console.log("Insert clicked once");
    const idx = dd.value;
    const code = window.macros[idx].code;
    await Word.run(async (context) => {
      context.document
             .getSelection()
             .insertText(code, Word.InsertLocation.replace);
      await context.sync();
    });
  };
});
